<template>
<div class="search">
    <van-nav-bar fixed title="商品列表" left-arrow @click-left="$router.go(-1)" />

    <van-search
      readonly
      shape="round"
      background="#ffffff"
      :value="$route.query.keyword || '搜索商品'"
      show-action
      @click="$router.push('/search')"
    >
      <template #action>
        <van-icon class="tool" name="apps-o" />
      </template>
    </van-search>

    <!-- 排序选项按钮 -->
    <div class="sort-btns">
      <div @click="getSearchList('all')" class="sort-item">综合</div>
      <div @click="getSearchList('sales')" class="sort-item">销量</div>
      <div @click="getSearchList('price')" class="sort-item">价格 </div>
    </div>

<div class="goods-list">
      <GoodsItem v-for="item in shopList" :key="item.goods_id" :items=" item"></GoodsItem>
    </div>
  </div>
</template>

<script>
import GoodsItem from '@/components/GoodsItem.vue'
import { getList } from '@/api/searchlist'

export default {
  name: 'SearchListIndex',
  components: {
    GoodsItem
  },
  data () {
    return {
      shopList: []
    }
  },
  // 跳转立马获取请求的keyword
  async created () {
    const res = await getList({
      sortType: 'all',
      categoryId: this.$route.query.categoryId,
      goodsName: this.$route.query.keyword
    })
    // 将结果存储到数组中
    this.shopList = res.data.list.data
  },
  methods: {
    // 按照销量或价格排序
    async  getSearchList (value) {
      const res = await getList({
        sortType: value,
        categoryId: this.$route.query.categoryId,
        goodsName: this.$route.query.keyword
      })
      this.shopList = res.data.list.data
    }
  }
}
</script>

<style lang="less" scoped>
.search {
  padding-top: 46px;

  ::v-deep .van-icon-arrow-left {
    color: #333;
  }

  .tool {
    font-size: 24px;
    height: 40px;
    line-height: 40px;
  }

  .sort-btns {
    display: flex;
    height: 36px;
    line-height: 36px;

    .sort-item {
      text-align: center;
      flex: 1;
      font-size: 16px;
    }
  }
}

// 商品样式
.goods-list {
  background-color: #f6f6f6;
}</style>
